
/* Customer Details */

#customer-details-container
{
    height: max-content;
}

#customer-details
{
    text-align: center;
    margin-top: 1em;
}

.details-column
{
    display: inline-block;
    vertical-align: top;
    width: 10em;
    font-size: 1.4em;
    text-align: left;
    margin: 0 3% 0 3%;
}

.field-cont
{
    width: max-content;
    margin-right: 3%;
    margin-bottom: 0.8em;
}

.field-cont .label, #customer-details .hidden-field-label
{
    font-weight: 500;
}

#customer-details input
{
    display: block;
}

#edit-cust-details, #cancel-cust-details, #save-cust-details
{
    padding: 0.5% 1%;
    background-color: rgb(255, 255, 255, 0.3);
}

#edit-cust-details:hover, #cancel-cust-details:hover, #save-cust-details:hover
{
    background-color: rgb(255, 255, 255, 0.5);
}

#edit-cust-details-text, #cancel-cust-details-text, #save-cust-details-text
{
    font-size: 1.4em;
    font-weight: 500;
}

#save-cust-details, #cancel-cust-details
{
    display: inline-block;
    margin-right: 4%;
}

.cust-edit-buttons
{
    width: 50%;
    text-align: left;
    padding-left: 15%;
    margin: auto;
    white-space: nowrap;
}

@media screen and (max-width: 1000px)
{
    .details-column
    {
        font-size: 1.1em;
    }

    #edit-cust-details-text, #cancel-cust-details-text, #save-cust-details-text
    {
        font-size: 1em;
        font-weight: 500;
    }
}

@media screen and (max-width: 600px)
{
    #customer-details-container
    {
        margin-top: 1em;
    }
}

/* Manage Tickets */

#manage-tickets-container
{
    height: 85%;
    margin-top: 1em;
}

.tickets-cont
{
    margin-top: 3%;
    height: 100%;
    overflow-y: auto;
}

.flight-cont
{
    background-color: rgb(255, 255, 255, 0.7);
    padding: 2%;
    margin: 1% 2%
}

.flight-departure
{
    display: inline-block;
    vertical-align: middle;
    margin-right: 3%;
}

.flight-departure *
{
    display: block;
}

.flight-airline
{
    margin-right: 3%;
}

#ticket-delete-error
{
    font-size: 1.2em;
    color: rgb(146, 0, 0);
    margin-top: 1%;
}

#delete-ticket-text
{
    font-size: 1.2em !important;
}

@media screen and (max-width: 1000px)
{
    #delete-ticket
    {
        display: block;
        margin: auto;
    }

    #delete-ticket-text
    {
        font-size: 1.3em !important;
    }

    #delete-ticket-icon
    {
        width: 1.4em;
    }

    .tickets-cont
    {
        height: 500px;
    }
}