
#flights-search-header
{
    margin-bottom: 2%;
}

/* Search Form */

#flights-search-form
{
    display: inline;
    font-weight: 600;
}

.country-select-container, .date-select-container
{
    display: inline-block;
    width: auto;
    margin: 1% 2% 1% 0;
}

#flights-search-form .form-control
{
    display: inline-block !important;
    width: 11em !important;
}

#trip-select-container
{
    display: inline-block;
}

#search-form-invalid-message
{
    color: rgb(136, 0, 0)
}

/* Search Results */

#loading-flights-results
{
    margin: 3% 0 3% 0;
}

#flights-results
{
    margin: 1% 0 2% 0;
}

#flights-results h5
{
    margin-top: 0.5em;
    margin-bottom: 1em;
}

.flights-pair, .flight
{
    cursor: pointer;
    transition: all .15s ease-in-out;
}

.flights-pair *, .flight *
{
    cursor: pointer;
}

.flights-pair:hover, .flight:hover
{
    transform: scale(1.04);
}

.flight
{
    white-space: nowrap;
    margin: 1% auto 1% auto;
    padding: 1% 1% 0.5% 1%;
    background-color: white;
    width: 40em;
    border-radius: 5%;
}

.airline-name, .flight-end, .offer, .plane-arrow
{
    display: inline-block;
    margin-right: 5%;
    vertical-align: middle;
}

.flight-start, .flight-span
{
    display: inline-block;
    margin-right: 1em;
    vertical-align: middle;
}

.origin-country, .departure-date, .destination-country, .landing-date, .ticket-price, .book-now
{
    display: block;
}

.plane-arrow
{
    width: 8em;
}

.offer
{
    padding: 0.5% 1% 0 1%;
    border: 1px dashed black;
}

.flights-pair-data
{
    display: inline-block;
    width: 80%;
}

.flights-pair
{
    white-space: nowrap;
    margin: 1% auto 1% auto;
    padding: 1% 1% 0.5% 1%;
    background-color: white;
    width: 40em;
    border-radius: 5%;
}

.offer-pair
{
    display: inline-block;
    vertical-align: text-bottom;
    padding: 0.5% 1% 0 1%;
    border: 1px dashed black;
}

#no-flights-message
{
    margin-top: 2%;
    font-weight: 500;
}

#loading-flights-page
{
  margin: 3em auto;
  width: 4em;
}

@media screen and (max-width: 1200px)
{
  .flight-cont
  {
    width: 80vw;
  }

  #search-btn
  {
    display: block;
    margin: auto;
  }
}

@media screen and (max-width: 750px)
{
  .flight, .flights-pair, .flight-cont
  {
      width: 34em;
  }
}

@media screen and (max-width: 580px)
{
  .flight, .flights-pair, .flight-cont
  {
      font-size: 0.8em;
  }
}

@media screen and (max-width: 470px)
{
  #flights-search-header
  {
      font-size: 1.4em;
  }
  #flights-search-form
  {
      font-size: 0.9em;
  }
  #flights-results h5
  {
    font-size: 1.1em;
  }
  .flight, .flights-pair, .flight-cont
  {
      width: 30em;
      font-size: 0.65em;
      white-space: nowrap;
  }
  .plane-arrow
  {
    width: 5em;
  }
  .flight-span
  {
    white-space: pre-wrap;
  }
}
