.user-container
{
    margin: 1%;
    background-color: rgb(255, 255, 255, 0.3);
    padding: 0.5% 0.5% 1% 0.5%;
}

.user-heading
{
    font-size: 2.5em;
    font-weight: 500;
    margin-bottom: 0.5em;
}

.nav-panel
{
    display: inline-block;
    vertical-align: top;
    width: 20em;
    height: 15em;
    background-color: rgba(139, 182, 218, 0.3);
    box-shadow: 0px 0px 5px white;
}

.nav-panel-option
{
    display: block;
    font-size: 2em;
    color: black;
}

.nav-panel-option:hover
{
    color: rgb(107, 156, 177);
    text-decoration: none;
}

.user-view
{
    position: relative;
    display: inline-block;
    width: 66vw;
    height: 70vh;
    padding: 0.5%;
    background-color: rgba(201, 231, 255, 0.3);
    box-shadow: 0px 0px 5px white;
}

.nav-select
{
    width: fit-content !important;
    margin: auto !important;
}

.user-view-mobile
{
    position: relative;
    height: max-content;
}

.loading-user-view
{
    top: 14vw;
    width: 6em;
    vertical-align: middle;
    margin: 20% auto;
}

.instructions
{
    font-size: 1.2em;
    font-weight: 500;
}

@media screen and (max-width: 590px)
{
    .instructions
    {
        font-size: 1em;
        margin-bottom: 0.5em;
    }

    .loading-user-view
    {
        width: 3em;
    }

    .user-heading
    {
        font-size: 1.5em;
    }
    
}

.list-view
{
    width: 97%;
    height: 80%;
    overflow-y: auto;
    margin: 1em auto 1em auto;
}

.loading-delete-element
{
    width: 3em;
    margin: 0 45%;
}

.loading-update-user
{
    display: inline-block;
    width: 1.4em;
    margin: 0 1%;
}

@media screen and (max-width: 1200px)
{
    .user-heading
    {
        font-size: 1.9em;
    }

    .nav-panel
    {
        width: 16em;
        height: 10em;
    }

    .nav-panel-option
    {
        font-size: 1.6em;
    }

    .user-view
    {
        width: 70vw;
    }

    .user-view .flight-cont
    {
        width: auto;
        font-size: 0.9em;
        padding: 0.8%;
    }

}

@media screen and (max-width: 1000px)
{
    .user-view
    {
        width: 85vw;
    }
}

@media screen and (max-width: 820px)
{
    #flight-cont-narrow
    {
        width: 95%;
        font-size: calc(0.5em + 0.5vw);
        padding: 0.8%;
    }

    .list-view
    {
        height: 75%;
    }
}

@media screen and (min-width: 2000px)
{
    .user-container
    {
        margin: 0.5%;
    }

    .user-view
    {
        height: 68vh;
    }
}