.App {
  text-align: center;
}

.bg-image
{
  z-index: -3;
  position: fixed;
  left: -15%;
  top: 0;
  width: 115%;
  animation: skyMove 30s ease-out;
}

@keyframes skyMove
{
  0% {
    transform: translate(12%, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}

/*#################*/
/* Mobile Support */

@media screen and (max-width: 1650px)
{
  .bg-image
  {
    width: 130%;
    content:url('../assets/sky-desktop.jpg');
  }
}

@media screen and (max-width: 1250px)
{
  .bg-image
  {
    content:url('../assets/sky-mobile.jpg');
  }
}

