#purchase-ticket-container
{
    width: max-content;
    padding: 1% 3%;
}

#purchase-heading
{
    margin-bottom: 1em;
}

.ticket-details
{
    text-align: left;
    display: inline-block;
}

.flight-heading
{
    margin: 3% 0 8% 0;
}

.ticket-field-cont
{
    margin: 2% 0;
    font-size: 1.3em;
}

.tickets-spacer
{
    display: inline-block;
    width: 3em;
}

#buy-ticket-btn
{
    cursor: pointer;
    font-size: 1.3em;
    font-weight: 500;
    color: rgb(13, 65, 133);
    white-space: nowrap;
    min-width: 20em;
    background-color: rgb(255, 255, 255, 0.5);
    border-radius: 5%;
    padding: 1%;
    margin: 0.8em 0;
}

#buy-ticket-btn:hover
{
    color: rgb(20, 96, 194);
}

#purchase-error
{
    font-weight: 500;
    font-size: 1em;
    color: rgb(146, 0, 0);
}

#loading-ticket-data
{
    margin-top: 5em;
    width: 5em;
}

@media screen and (max-width: 639px)
{
    .tickets-spacer
    {
        width: 1em;
    }
}

@media screen and (max-width: 590px)
{
    .tickets-spacer
    {
        display: none;
    }
}

@media screen and (max-width: 500px)
{
    #buy-ticket-btn
    {
        min-width: 10em;
        white-space: pre-wrap;
    }
}