.password-change
{

}

.password-change-label
{
    font-weight: 500;
}

.password-change-icon
{
    width: 1em;
    filter: hue-rotate(180deg);
}

.password-change-icon:hover
{
    cursor: pointer;
}

.popup-container
{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(255, 255, 255, 0.6);
}

.change-password-popup
{
    position: absolute;
    top: 5%;
    left: 5%;
    padding: 2%;
    background-color: white;
}

.change-password-buttons
{
    margin-top: 8%;
}

#cancel-change-password, #confirm-change-password
{
    margin-right: 5%;
}

.password-change-loading
{
    width: 2vw;
}