#grid-container
{
  height: 75vh;
  margin: 3vw;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto;
  gap: 1em 1em;
  overflow: hidden;
}

#home-grid-box-1, #home-grid-box-2, #home-grid-box-3
{
  position: relative;
}

#home-grid-box-2
{
  align-content: center;
  vertical-align: middle;
}

#home-grid-box-3
{
  grid-column: 1/3;
}

/* Welcome Elements */

#plane
{
  position: relative;
  top: 10%;
  width: 35vw;
  opacity: 0.8;
  z-index: -2;
  animation: planeFly 24s ease-out;
}

@keyframes planeFly
{
  0% {
    transform: translate(18%, 18%);
    opacity: 0.3;
  }
  5% {
    transform: translate(15%, 15%);
    opacity: 0.8;
  }
  100% {
    transform: translate(0, 0);
  }
}

#clouds
{
  position: absolute;
  opacity: 0;
  top: -20%;
  left: -5%;
  width: 35vw;
  z-index: -1;
  animation: cloudsMove 10s linear;
}

@keyframes cloudsMove
{
  0% {
    transform: translate(0, 0);
    opacity: 0.5;
  }
  50% {
    transform: translate(10%, 5%);
    opacity: 0.5;
  }
  100% {
    transform: translate(20%, 10%);
    opacity: 0;
  }
}

h1#welcome-to
{
  font-size: calc(22px + 2vw);
}

h1#flights-center
{
  font-size: calc(26px + 3vw);
}

#site-desc-cont
{
  margin: auto;
  margin-top: 6%;
  padding: 1%;
  width: calc(150px + 22vw);
  background-color: rgb(255, 255, 255, 0.15);
  border-radius: 5%;
}

#site-description
{
  font-size: calc(10px + 1vw);
}

/* Offer Buttons */

#offers-container
{
  width: 100%;
}

.offer-btn-cont
{
  display: inline-block;
  width: max-content;
  font-size: calc(18px + 1vw);
  color: black;
  transition: all .15s ease-in-out;
  border-radius: 50%;
  box-shadow: 0px 0px 10px white;
  padding: 2% 4% 2% 4%;
  margin: 0 6% 0 6%;
}

.offer-btn-cont:hover
{
  transform: scale(1.1);
  cursor: pointer;
}

.offer-btn-cont *:hover
{
  cursor: pointer;
}

a.offer-btn-cont:hover
{
  text-decoration: none;
  color: black;
}

#find-flight
{
  background-color: rgb(190, 226, 255, 0.7);
}

#deals
{
  background-color: rgb(189, 255, 187, 0.7);
}

/* Wide screens support */

@media screen and (min-width: 1921px)
{
  #grid-container
  {
    height: 70vh;
    grid-template-rows: 40% 40%;
    row-gap: 30%;
    margin: 2%;
  }
}

/* Mobile support */

@media screen and (max-width: 1400px)
{
  #grid-container
  {
    height: 77vh;
    grid-template-rows: 50% 40%;
  }
}

@media screen and (max-width: 1000px)
{
  #grid-container
  {
    height: 83vh;
    display: block;
    row-gap: 20em;    
    margin-top: 1em;
  }

  #home-grid-box-1, #home-grid-box-2, #home-grid-box-3
  {
    padding: 1.5em 0;
  }

  #clouds
  {
    animation: none;
  }

  #plane
  {
    width: 50vw;
  }

  #site-desc-cont
  {
    margin-bottom: 3%;
  }

  .offer-btn-cont
  {
    margin: 3%;
  }
}

@media screen and (max-width: 500px)
{
  #grid-container
  {
    height: 75vh;
  }
}