
/* Airline Details */

#airline-details
{
    text-align: center;
    margin-top: 3%;
}

#airline-countries-loading
{
    position: relative;
    top: 25%;
}

.airline-column
{
    display: inline-block;
    vertical-align: top;
    width: 20vw;
    font-size: 1.6vw;
    text-align: left;
    margin: 1% 3% 0 3%;
}

#airline-details .hidden-field-label
{
    font-weight: 500;
}

#airline-details input
{
    display: block;
}

#edit-air-details, #cancel-air-details, #save-air-details
{
    padding: 0.5% 1%;
    background-color: rgb(255, 255, 255, 0.3);
}
#edit-air-details:hover, #cancel-air-details:hover, #save-air-details:hover
{
    background-color: rgb(255, 255, 255, 0.5);
}

#edit-air-details-text, #cancel-air-details-text, #save-air-details-text
{
    font-size: 1em;
    font-weight: 400;
}

#save-air-details, #cancel-air-details
{
    display: inline-block;
    margin-right: 4%;
}

.air-edit-buttons
{
    width: 50%;
    text-align: left;
    padding-left: 10%;
    margin: auto;
}

.airline-details .country-select
{
    display: block;
}

@media screen and (max-width: 900px)
{
    #airline-details
    {
        margin-top: 2em;
    }
    #airline-details-heading
    {
        display: none;
    }
}

/* Manage Flights */

#manage-flights-container
{
    margin-top: 1em;
    height: 72vh;
}

#filters-header
{
    margin-right: 1%;
    font-weight: 500;
}

#origin-country-filter, #dest-country-filter
{
    display: inline-block;
    width: fit-content;
    margin: 0 1%;
}

#airline-flights-list
{
    height: 70%;
}

#delete-flight
{
    filter: saturate(0.9);
}

#no-airline-flights
{
    margin-top: 4%;
}

#filter-loading
{
    width: 3em;
    margin:auto
}

@media screen and (max-width: 800px)
{
    #flights-filter-form
    {
        font-size: 0.9em;
    }
    
    #filters-header
    {
        display: block;
        margin: auto;
    }
    
    #flights-filter-form select
    {
        margin-bottom: 1em;
    }

    #manage-flights-instructions
    {
        display: none;
    }

    #airline-flights-list
    {
        height: 80%;
    }
}

@media screen and (max-width: 1000px)
{
    #manage-flights-container
    {
        height: 65vh;
    }
}

@media screen and (max-width: 600px)
{
    #manage-flights-container
    {
        height: 60vh;
    }
}


/* Flight Container */

.flight-extra-details
{
    margin-right: 4%;
}

#edit-flight, #delete-flight
{
    display: block;
}

#delete-flight-error, #flight-error
{
    font-size: 1.05em;
    color: rgb(146, 0, 0);
}

.flight-departure-edit
{
    display: inline-block;
    vertical-align: middle;
    text-align: left;
    margin-right: 2%;
}

.flight-origin-country, .flight-landing, .flight-destination-country, .flight-extra-details, .flight-buttons
{
    display: inline-block;
    vertical-align: middle;
}

.flight-origin-country, .flight-landing, .flight-destination-country
{
    text-align: left;
    margin-right: 2%;
}

#flight-origin-country-field, #flight-destination-country-field
{
    width: fit-content;
}

#flight-ticket-price-edit-field, #flight-remaining-tickets-field
{
    width: 5em;
}

#flight-ticket-price-edit-field, #flight-remaining-tickets-field
{
    display: inline-block;
}

.flight-ticket-price-edit, .flight-remaining-tickets-edit
{
    display: inline-block;
    margin-top: 2%;
    margin-right: 2%;
}


@media screen and (max-width: 590px)
{
    .flight-extra-details
    {
        display: block;
    }

    .remain-cont, .price-cont
    {
        display: inline-block;
        margin-right: 1em;
    }

    #flight-cont-narrow
    {
        width: 29em;
    }

    #edit-flight, #delete-flight
    {
        display: inline-block;
        font-size: 1.3em;
        margin-right: 1em;
    }

    .flight-departure-edit, .flight-origin-country, .flight-destination-country, .flight-landing, .flight-ticket-price-edit, .flight-remaining-tickets-edit
    {
        display: block;
        width: fit-content;
    }

    .flight-departure-title, .flight-origin-country-title, .flight-landing-title, .flight-destination-country-title, 
    .flight-ticket-price-edit-title, .flight-remaining-tickets-title
    {
        margin-top: 0.5em;
        font-size: 1.3em;
    }

    #cancel, #save
    {
        margin-top: 1em;
        margin-right: 1em;
        font-size: 1.4em;
    }
}

/* Create Flight */

#create-flight-container
{
    height: max-content;
}

#create-flight
{
    margin-top: 2%;
}

#create-flight label
{
    font-weight: 500;
}

.create-flight-buttons
{
    margin-top: 3%;
}

#clear-fields, #create-flight-btn
{
    margin-right: 0.6em;
    background-color: rgb(255, 255, 255, 0.3);
}

#clear-fields:hover, #create-flight-btn:hover
{
    background-color: rgb(255, 255, 255, 0.5);
}

#clear-fields-text, #create-flight-btn-text
{
    font-size: 1em;
    font-weight: 400;
}

#clear-fields-icon, #create-flight-btn-icon
{
    width: 1em;
}

#create-flight-spacer
{
    display: inline-block;
    width: 2.5vw;
}

@media screen and (max-width: 590px)
{
    #create-flight
    {
        margin-top: 1.7em;
    }
    #create-flight-heading
    {
        display: none;
    }
    #create-flight-spacer
    {
        display: none;
    }
}
