/* ****** */
/* Inbox */

@media screen and (max-width: 590px)
{
    #inbox-container
    {
        margin-top: 1em;
    }
}

#total-pending-airlines
{
    font-size: 1em;
    font-weight: 500;
}

#pending-airlines-list
{
    height: max-content;
}

/* Airline Container */

.airline-cont
{
    margin: 1% 0;
    text-align: left;
    background-color: rgb(255, 255, 255, 0.5);
}

.airline-cont *
{  
    font-size: 1.05em;
}

/* First row */

.customer-first-row *
{
    display: inline-block;
}

.airline-icon
{
    width: 1.8em;
    margin: 1% 1.5% 1% 1%;
}

.airline-name, .airline-email
{
    margin-right: 0.7em;
}

/* Second row */

.airline-second-row
{
    display: block;
    padding-left: 6.5vw;
}

.airline-username-cont, #airline-password, .airline-country-cont
{
    display: inline-block;
    margin-right: 0.7em;
}

.airline-username-label, .airline-password-label, .airline-country-label
{
    display: inline-block;
}

.airline-password
{
    display: inline-block;
}

.password-text
{
    font-weight: 300;
}

.airline-username, .airline-country
{
    display: inline-block;
    font-weight: 300;
}

/* Errors */

.airline-action-error
{
    margin-left: 6.5vw;
    color: rgb(148, 0, 0)
}

.airline-countries-error
{
    margin-left: 6.5vw;
    color: rgb(148, 0, 0)
}

@media screen and (max-width: 590px)
{
    .airline-cont *
    {  
        font-size: 1em;
    }

    #total-pending-airlines
    {
        font-size: 0.9em;
    }
}

/* **************** */
/* Manage Customers */

#customers-filter-form *
{
    display: inline-block !important;
    font-weight: 500;
}

#name-filter
{
    width: 5em;
    margin-left: 0.6em;
    margin-right: 1em;
}

#id-filter
{
    width: 4em;
    margin-left: 0.6em;
    margin-right: 1em;
}

#customers-list
{
    height: 56vh;
}

/* Customer Container */

.customer-cont
{
    margin: 1% 0;
    text-align: left;
    background-color: rgb(255, 255, 255, 0.5);
}

.customer-cont *
{  
    font-size: 1em;
}

/* First row */

.customer-first-row *
{
    display: inline-block;
}

.customer-icon
{
    width: 1.8em;
    margin: 1% 1.5% 1% 1%;
}

.customer-id, .customer-name, .customer-email
{
    margin-right: 0.6em;
}

/* Second row */

.customer-second-row
{
    display: block;
    padding-left: 6.5vw;
}

.customer-username-cont, #customer-password, .customer-address-cont, .customer-phone-cont
{
    display: inline-block;
    margin-right: 0.5em;
}

.customer-username-label, .customer-password-label, .customer-address-label, .customer-email, .customer-phone-label
{
    display: inline-block;
}

#customer-password-text
{
    font-weight: 300;
}

.customer-username, .customer-address, .customer-phone
{
    display: inline-block;
    font-weight: 300;
}

/* Edit customer */

.customer-third-row
{
    height: max-content;
    margin-top: 1%;
    padding-left: 6.5vw;
}

.customer-cont input
{
    width: 10em;
}

#customer-action-error
{
    font-size: 1em;
    color: rgb(131, 1, 1);
    padding-left: 6.5vw;
}

@media screen and (max-width: 590px)
{
    #customers-list
    {
        height: 45vh;
    }

    #customers-container
    {
        margin: 1.4em 0;
    }

    #manage-customer-instructions
    {
        display: none;
    }

    .filter-field-cont
    {
        display: block;
        white-space: nowrap;
    }

    #total-customers
    {
        display: block !important;
        margin-top: 0.5em;
    }
}

/* *************** */
/* Manage Airlines */

#airlines-container
{
    margin-top: 1.1em;
}

.filter-field-cont
{
    display: inline-block;
}

#airlines-container
{
    height: 63vh;
}

#airlines-filter-form 
{
    display: inline-block !important;
    font-weight: 500;
}

.airline-id
{
    margin-right: 2%;
}

.airline-third-row
{
    margin-left: 6.5vw;
}

.airline-error, #airline-update-error
{
    color: rgb(146, 0, 0);
    margin: 0.5% 1%;
}

@media screen and (max-width: 590px)
{   
    #manage-airlines-instructions
    {
        display: none;
    }

    .filter-field-cont
    {
        display: block;
        white-space: nowrap;
    }
}