#about-container
{
    padding: 0.5% 6% 1% 6%;
    max-width: 1000px;
}

#about-container a
{
    color: #1e65bd;
}

#about-image
{
    width: 90%;
    max-width: 100%;
    border-radius: 2%;
    opacity: 0.8;
    margin-bottom: 1%;
}

#about-notice
{
    width: 80%;
    font-size: 1em;
    padding: 1%;
    margin: 0 auto 2% auto;
    background-color: rgb(255, 255, 255, 0.3);
}

#notice-label
{
    font-weight: 500;
}

#about-welcome-heading
{
    margin-bottom: 2%;
}

#about-welcome, #git-hub-reference, #contact-email
{
    font-size: 1.2em;
}