#login-container
{
    width: 27vw;
    text-align: left;
    padding: 2% 2.5% 1.5% 0.8em;
}

#login-container h2
{
    margin-bottom: 4%;
}

#login-form label
{
    display: block;
    font-weight: 500;
}

#username, #password
{
    margin-bottom: 2%;
    width: 16em;
}

#login-btn
{
    display: inline-block;
    margin: 5% 5% 0 0;
}

#sign-up-container
{
    margin-top: 4%;
}

#new-user
{
    font-weight: 500;
}

#sign-up-container a
{
    color: rgb(31, 90, 145);
    font-weight: 500;
}

#loading-login
{
    display: inline-block;
    width: 2em;
    vertical-align: bottom;
}

#login-message
{
    margin-top: 4%;
    color: rgb(163, 4, 4);
}