
.page-content-container
{
  width: 65vw;
  padding: 0.5%;
  border-radius: 1%;
  margin: 1% auto 1% auto;
  background-color: rgb(255, 255, 255, 0.4);
}

.info-icon
{
  display: inline-block;
  width: 1em;
}

/* iFrames */

.iframe-cont
{
  overflow-y: auto;
  border: hidden;
  width: 100%;
  height: 98%;
  min-height: 34em;
}

/* Flights Board */

#flights-board-container
{
  overflow: hidden;
  width: 75vw;
  height: 77vh;
}

/* Deals Page */

#deals-page-container
{
  overflow: auto;
  width: 75vw;
  height: 77vh;
}

/* Register pages */

#customer-register-container
{
  overflow: hidden;
  width: 60vw;
  height: 45em;
}

#airline-register-container
{
  overflow: hidden;
  width: 60vw;
  height: 45em;
}

/* FC-Buttons */

.fc-button
{
  display: inline-block;
  width: max-content;
  border-radius: 8%;
  padding: 0 0.5%;
}

.fc-button:hover
{    
  cursor: pointer;
  background-color: rgb(255, 255, 255, 0.5);
}

.fc-button *:hover
{
  cursor: pointer;
}

.fc-button-text
{
  display: inline-block;
  font-size: 1.4em;
  font-weight: 300;
  user-select: none;
}

.fc-button-icon
{
  display: inline-block;
  width: 1.4em;
  margin: 1% 0;
}

@media screen and (max-width: 900px)
{
  .fc-button
  {
    font-size: 1em;
  }
  .fc-button-text
  {
    font-size: 1em;
  }
  .fc-button-icon
  {
    width: 1em;
  }
}

/* Hidden fields */

.hidden-field
{
  width: max-content;
}

.hidden-field-label, .hidden-field-text
{
  display: inline-block;
}

.hidden-field-icon
{
  display: inline-block;
  width: 1.4em;
  cursor: pointer;
}

/* Errors */

.error-container
{
  margin: 2% 0;
}

.error-icon
{
  display: inline-block;
  width: 1.4em;
  opacity: 0.75;
  margin-right: 1%;
}

.error-message
{
  display: inline-block;
  font-weight: 400;
  font-size: 1.1em;
  margin-bottom: 0;
  vertical-align: bottom;
}

/*#################*/
/* Mobile Support */

@media screen and (max-width: 1300px)
{
  .page-content-container
  {
    width: 96vw !important;
  }
}

@media screen and (max-width: 1100px)
{
  #deals-page-container
  {
    height: 88vh;
  }

  #flights-board-container
  {
    height: 88vh;
  }

  #customer-register-container
  {
    height: 88vh;
  }

  #airline-register-container
  {
    height: 88vh;
  }

  .info-icon
  {
    display: none;
  }
}

@media screen and (max-width: 600px)
{
  #deals-page-container
  {
    height: 80vh;
  }

  #flights-board-container
  {
    height: 80vh;
  }

  #customer-register-container
  {
    height: 78vh;
  }

  #airline-register-container
  {
    height: 78vh;
  }
}

@media screen and (min-width: 1900px)
{
  .page-content-container
  {
    margin: 0.5% auto 0.5% auto;
  }
}