
.top-navbar
{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(255, 255, 255,0.65);
  height: calc(50px + 4vw);
  padding: 0.5% 2% 0.5% 0.2%;
}

.site-logo 
{
  display: inline-block;
  width: calc(70px + 5vw);
  margin-left: 0.75%;
}

.site-logo:hover
{
  cursor: pointer;
}

.nav-options
{
  display: inline-block;
  width: calc(75vw - 70px);
}

.nav-option
{
  color: rgb(48, 121, 230);
  font-weight: 600;
  font-size: calc(14px + 0.7vw);
  margin: 1.5vmin;
}

a.nav-option:hover
{
  text-decoration: none;
  color: rgba(48, 121, 230, 0.671);
}

/* ^^^^^^^^^^^^^^^ */
/* Mobile Nav Menu */

#mobile-nav-menu-btn
{
  display: inline-block;
  height: max-content;
}

#mobile-nav-menu-btn-text
{
  font-size: 1.4em;
  font-weight: 500;
  margin: 0;
}

#mobile-nav-menu-btn-icon
{
  width: 1.2em;
  vertical-align: sub;
}

.mobile-menu
{
  position: absolute;
  left: 0;
  top: calc(50px + 4vw);
  width: 100vw;
  height: 100vw;
  z-index: 1;
  background-color: rgb(255, 255, 255, 0.9);
}

.mobile-nav-option
{
  display: block;
  font-size: 1.6em;
  margin: 3% 0;
}

#close-menu-btn
{
  display: block;
  margin: auto;
  width: 8em;
}

#close-menu-btn-text
{
  font-size: 1.4em;
  font-weight: 400;
  color: rgb(9, 61, 116);
}

#close-menu-btn-icon
{
  width: 1.1em;
  opacity: 0.8;
}

/* ######################### */
/* User section and dropdown */

#user-cont-desktop
{
  width: 14em;
}

.user-actions
{
  display: inline-block;
  height: max-content;
  border-radius: 5%;
}

.user-actions span
{
  display: inline-block;
}

.user-actions a:hover
{
  text-decoration: none;
}

#sign-up-btn, #login-user-btn
{
  background-color: rgb(255, 255, 255, 0.3);
  padding: 0 3%;
}

#sign-up-btn
{
  margin-right: 1%;
}

#sign-up-btn-text, #login-user-btn-text
{
  font-size: 1.3em;
  font-weight: 500;
}

#sign-up-btn:hover, #login-user-btn:hover
{
  background-color: rgb(255, 255, 255, 0.5);
}

#sign-up-btn-icon, #login-user-btn-icon
{
  width: 1em;
}

/* ^^^^^^^^^^^^^^^^^ */
/* Mobile Login Menu */

#mobile-login-menu-btn-text
{
  font-size: 1.4em;
  font-weight: 500;
  margin-bottom: 0;
}

#mobile-login-menu-btn-icon
{
  width: 1.4em;
}

#sign-up-btn-mobile, #login-user-btn-mobile
{
  display: block;
  margin: 1em auto 1em auto;
}

#sign-up-btn-mobile-text, #login-user-btn-mobile-text
{
  font-size: 1.4em;
  font-weight: 400;
}

#sign-up-btn-mobile-icon, #login-user-btn-mobile-icon
{
  width: 1.5em;
}

/* ################ */
/* Users menu */

.logged-in-greet
{
  width: 11em;
  padding-top: 0.3em;
  padding-bottom: 0.3em;
  background-color: rgb(255, 255, 255, 0.4);
}

.user-greet
{
  font-size: 1.1em;
  font-weight: 500;
  color: rgb(70, 115, 181);
  margin-right: 5%;
  margin-bottom: 0;
}

.person-icon
{
  width: 1.5em;
  vertical-align: middle;
}

.dropdown-head
{
  position: relative;
  height: 100%;
}

.dropdown-head:hover .dropdown-content, .dropdown-head *:hover .dropdown-content
{
  opacity: 1;
  transition: 0.2s;
}

.dropdown-content 
{
  position: absolute;
  width: 100%;
  margin-top: 0;
  background-color: rgb(189, 207, 222, 0.8);
  opacity: 0;
  transition: 0.2s;
}

.dropdown-content *
{
  display: block;
}

.user-page-btn, .logout-btn
{
  font-size: 1.3em !important;
  color: black;
  width: 100%;
  padding: 0.2em 0 0.2em 0 !important;
}

.user-page-btn:hover, .logout-btn:hover
{
  background-color: rgb(223, 231, 238);
}

.user-icon, .logout-icon
{
  display: inline-block;
  width: 1.6em;
  margin-left: 5%;
}

/* ^^^^^^^^^^^^^^^^^ */
/* Users menu */

.mobile-user-name
{
  width: 7em;
  padding-top: 0.3em;
  background-color: rgb(255, 255, 255, 0.4);
}

.greeting-container:hover, .greeting-container *:hover
{
  cursor: pointer;
}

#user-page-btn-mobile, #logout-btn-mobile
{
  display: block;
  margin: 0.4em auto 0.4em auto;
}

#user-page-btn-mobile-text, #logout-btn-mobile-text
{
  font-size: 1.4em;
  font-weight: 400;
}

#user-page-btn-mobile-icon, #logout-btn-mobile-icon
{
  width: 1.5em;
}
